/* css memberpress tutup */
.wss-card {
  background-color: #191919;
  border-radius: 0 0 10px 10px;
}

.wss-card h3 {
  padding: 10px 15px;
  color: #fff;
}

.wss-d-flex {
  display: flex;
}

.wss-position-relative {
  position: relative;
}

.wsss-flex-wrap {
  flex-wrap: wrap;
}

.wss-w-50 {
  width: 50%;
}

.wss-w-100 {
  width: 100%;
}

/* medium md */
@media (min-width: 768px) {
  .wss-w-md-25 {
    width: 25%;
  }
  .wss-w-md-33 {
    width: 33.3%;
  }
}
.wss-floating-badge {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 3px 9px;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background-color: #ff4f4f;
}

.wss-question-frame {
  color: #bfbfbf;
}

.wss-p-1 {
  padding: 0.7em;
}

.wss-pt-1 {
  padding-top: 0.7em;
}

.wss-pl-2 {
  padding-left: 2em;
}

.wss-pt-3 {
  padding-top: 3em;
}

.wss-pt-2 {
  padding-top: 2em;
}

.wss-m-1 {
  margin: 0.7em;
}

.wss-mb-2 {
  margin-bottom: 2em;
}

.wss-d-none {
  display: none;
}

.wss-d-block {
  display: block;
}

.wss-d-iline-block {
  display: inline-block;
}

.wss-align-item-end {
  justify-content: flex-end !important;
}

.video-container {
  position: relative;
  width: 100%;
  padding-top: 56.25%; /* 16:9 aspect ratio (h/w * 100) */
  overflow: hidden;
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.questionnaire-menu a {
  padding: 10px 15px;
}

.questionnaire-menu .menu-item:hover a,
.questionnaire-menu .current-menu-item a {
  color: #fff;
}

.wss-heading-page {
  font-size: 22px;
  border-left: 4px solid #ff4f4f;
  padding: 3px 12px;
  margin-bottom: 20px;
  background: #000;
  font-weight: 700;
  color: #fff;
}

.question-title {
  font-size: 19px;
  border-left: 4px solid #ff4f4f;
  padding: 3px 12px;
  margin-bottom: 20px;
  background: #000;
  font-weight: 700;
}

.wss-question-frame input[type=radio] + label {
  position: relative;
}

.wss-question-frame input[type=radio] + label::before {
  content: "";
  background-image: url("../svg/circle.svg");
  width: 24px; /* Sesuaikan dengan ukuran SVG Anda */
  height: 24px;
  display: inline-block;
  position: absolute;
  left: 0px;
  background-size: cover;
}

.wss-question-frame input[type=radio]:checked + label::before {
  content: "";
  background-image: url("../svg/check2-circle.svg");
}

.wss-cursor-pointer {
  cursor: pointer;
}

.wss-btn-submit {
  border-radius: 7px;
  padding: 7px 15px;
  border: none;
  color: #fff;
  background: #ff4f4f;
}

.wss-notice {
  background-color: #0e0e0e;
  color: #e3e3e3;
  padding: 10px 20px;
  margin: 15px 0;
  border: 1px solid #262626;
  border-radius: 4px;
  position: relative;
}

.wss-notice svg {
  display: inline;
}

.wss-notice a,
.wss-notice a:visited {
  color: #e3e3e3;
  text-decoration: none;
}

.wss-notice a:hover {
  color: #ff4f4f;
}

.wss-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #721c24;
  text-decoration: none;
  cursor: pointer;
}

.wss-btn-submit:hover {
  background: #333333;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.wss-loading {
  animation: spin 1s linear infinite; /* Ganti dengan durasi atau properti animasi yang sesuai */
}

.wss-container {
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
}

.wss-pagination .page-numbers {
  padding: 3px 10px;
  margin: 2px;
  border-radius: 3px;
  border: #191919;
  background-color: #3b4351;
}

.wss-pagination .page-numbers.current,
.wss-pagination .page-numbers:hover {
  background-color: #ff4f4f;
  border: #191919;
  color: #fff;
}

.wss-text-light {
  color: #bfbfbf;
}

.wss-gap-20 {
  gap: 20px;
}

.wss-title {
  background: #fff;
  color: #333;
  border-radius: 3px;
  border-left: 4px solid #333;
}

.wss-ratio {
  position: relative;
  overflow: hidden;
}

.wss-ratio-3x4::before {
  content: "";
  display: block;
  padding-bottom: 75%;
}

.wss-ratio img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

.wss-container .nav-links {
  padding: 0px 20px;
}

.wss-container .nav-links .page-numbers {
  padding: 10px;
}

